.lesson-list {
  border: 1px solid #DBE2DF;
  border-radius: 16px;
  overflow: hidden;
}
.lesson-list .lesson-title {
  background: #d0eae2;
  padding: 15px;
  display: block;
  position: relative;
  color: #034C31;
}
.lesson-list .lesson-title::after {
  content: "";
  background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M3.7041 8.20427C3.80862 8.09939 3.93281 8.01617 4.06956 7.95939C4.2063 7.90261 4.35291 7.87338 4.50098 7.87338C4.64904 7.87338 4.79565 7.90261 4.9324 7.95939C5.06914 8.01617 5.19334 8.09939 5.29785 8.20427L12 14.9065L18.7041 8.20427C18.9154 7.99292 19.2021 7.87419 19.501 7.87419C19.7999 7.87419 20.0865 7.99292 20.2979 8.20427C20.5092 8.41561 20.6279 8.70226 20.6279 9.00114C20.6279 9.30003 20.5092 9.58667 20.2979 9.79802L12.7979 17.298C12.6933 17.4029 12.5691 17.4861 12.4324 17.5429C12.2957 17.5997 12.149 17.6289 12.001 17.6289C11.8529 17.6289 11.7063 17.5997 11.5696 17.5429C11.4328 17.4861 11.3086 17.4029 11.2041 17.298L3.7041 9.79802C3.59922 9.6935 3.51601 9.56931 3.45923 9.43256C3.40245 9.29582 3.37322 9.14921 3.37322 9.00114C3.37322 8.85308 3.40245 8.70647 3.45923 8.56972C3.51601 8.43298 3.59922 8.30878 3.7041 8.20427Z' fill='%23023D28'/%3e%3c/svg%3e");
  position: absolute;
  right: 15px;
  top: 50%;
  margin-top: -12px;
  width: 24px;
  height: 24px;
  transition: 0.2s all;
}
.lesson-list .lesson-title h3 {
  font-size: 18px;
  margin-bottom: 5px;
  line-height: 1.4;
  padding-right: 30px;
}
.lesson-list .lesson-title .flex {
  gap: 20px;
  color: #656867;
}
.lesson-list .lesson-title .flex .td {
  position: relative;
}
.lesson-list .lesson-title .flex .td.td + .td::before {
  content: "";
  background: #656867;
  width: 4px;
  height: 4px;
  position: absolute;
  border-radius: 50%;
  left: -12px;
  top: 9px;
}
.lesson-list .lesson-title .progress {
  background: #D0EAE2;
  height: 13px;
  border-radius: 4px;
  position: relative;
  margin-top: 10px;
}
.lesson-list .lesson-title .progress .step {
  position: absolute;
  background: linear-gradient(279deg, #2E8B57 0%, #A0D9BA 100%);
  height: 13px;
  border-radius: 50px;
  left: 0;
  top: 0px;
  transition: 0.2s all;
  color: #fff;
  text-align: right;
  font-size: 12px;
  line-height: 1.2;
  padding: 0 5px;
  min-width: 28px;
}
.lesson-list .lesson-title[aria-expanded=true] {
  border-bottom: 1px solid #DBE2DF;
}
.lesson-list .lesson-title[aria-expanded=true]::after {
  transform: rotate(180deg);
}
.lesson-list .lesson {
  padding: 12px 0px 12px 52px;
  position: relative;
  cursor: pointer;
}
.lesson-list .lesson .lesson-step {
  position: absolute;
  width: 3px;
  background: #dbe2df;
  top: 0;
  bottom: 0;
  left: 28px;
}
.lesson-list .lesson .lesson-step span {
  color: #656867;
  width: 26px;
  height: 26px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 3px solid #DBE2DF;
  border-radius: 50%;
  background: #fff;
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  margin: -13px 0 0 -13px;
}
.lesson-list .lesson .lesson-step .help {
  background: #ff5e57;
  color: #fff;
  border: 0;
}
.lesson-list .lesson .lesson-step .help svg {
  width: 18px;
  height: 18px;
}
.lesson-list .lesson .title {
  font-size: 18px;
  font-weight: 400;
  line-height: 1.5;
}
.lesson-list .lesson .info-lesson {
  display: flex;
  column-gap: 30px;
  row-gap: 30px;
}
.lesson-list .lesson .info-lesson .status {
  color: #034C31;
  font-weight: 300;
  display: none;
}
.lesson-list .lesson .info-lesson .end-view {
  color: #FF5E57;
  font-weight: 300;
}
.lesson-list .lesson .info-lesson .remaining-views {
  color: #034C31;
  font-weight: 300;
}
.lesson-list .lesson .info-lesson .item {
  color: #656867;
  font-weight: 300;
  display: flex;
  align-items: center;
  gap: 4px;
  position: relative;
}
.lesson-list .lesson .info-lesson .item .icon-svg {
  width: 20px;
  flex-shrink: 0;
}
.lesson-list .lesson .info-lesson .item.item + .item::before {
  content: "";
  background: #dbe2df;
  width: 4px;
  height: 4px;
  position: absolute;
  border-radius: 50%;
  left: -15px;
}
.lesson-list .lesson::after {
  content: "";
  background: #DBE2DF;
  position: absolute;
  height: 1px;
  right: 0;
  left: 52px;
  bottom: 0;
}
.lesson-list .lesson:last-child::after {
  display: none;
}
.lesson-list .lesson.active {
  background: #d0eae2;
}
.lesson-list .lesson.succsess .lesson-step {
  background: #2D8755;
}
.lesson-list .lesson.succsess .lesson-step span {
  color: #00110B;
  border: 3px solid #2D8755;
}
.lesson-list .lesson.succsess .info-lesson .status {
  display: block;
}
.lesson-list .card-body {
  border: 0;
  border-radius: 0 0 16px 16px;
  padding: 0 15px 0 0;
}
.lesson-list .card-body .lesson-title {
  background: none !important;
  margin-right: -15px;
}
.lesson-list .card-body ul {
  margin-bottom: 0;
}
.lesson-list .card-body ul li .lesson-title {
  border-top: 1px solid #dbe2df;
  border-bottom: 1px solid #dbe2df;
}
.lesson-list .card-body ul li .lesson-title::after {
  top: 50%;
  margin-top: -12px;
}
.lesson-list .card-body ul li:first-child .lesson-title {
  border-top: 0;
}
.lesson-list.border {
  border-radius: 16px;
  overflow: hidden;
}
.lesson-list.you .lesson-title {
  background: var(--bg-main);
}
.lesson-list.you .lesson-title::after {
  top: 15px;
  margin-top: 0;
}
.lesson-list.you .lesson .title {
  font-size: 16px;
}
.lesson-list.you .lesson .flex-2 {
  flex-shrink: 0;
}
@media (max-width: 600px) {
  .lesson-list .card-body {
    padding: 0;
  }
  .lesson-list .card-body .lesson-title {
    margin-right: 0;
  }
  .lesson-list .lesson-title {
    padding: 10px 10px 10px 15px;
  }
  .lesson-list .lesson-title::after {
    background-size: contain;
    width: 20px;
    height: 20px;
    right: 10px;
  }
  .lesson-list .lesson-title h3 {
    font-size: 15px;
    padding-right: 15px;
  }
  .lesson-list .lesson-title .flex {
    font-size: 13px;
    font-weight: 300;
  }
  .lesson-list .lesson {
    padding: 8px 10px 8px 42px;
  }
  .lesson-list .lesson .title {
    font-size: 14px;
    font-weight: 500;
  }
  .lesson-list .lesson .lesson-step {
    left: 20px;
  }
  .lesson-list .lesson .info-lesson {
    column-gap: 15px;
    row-gap: 15px;
    font-size: 12px;
  }
  .lesson-list .lesson .info-lesson .item .icon-svg {
    width: 16px;
  }
  .lesson-list .lesson .info-lesson .item.item + .item::before {
    left: -8px;
  }
  .lesson-list.you .lesson .title {
    font-size: 14px;
  }
  .lesson-list.you .lesson .flex-2 {
    flex-shrink: 0;
  }
  .lesson-list.you .lesson .flex-2 .btn-default {
    padding: 5px 10px;
  }
}

@media (min-width: 992px) {
  .main-detail__col1 .lesson-list, .main-detail__col3 .lesson-list {
    border: 0;
    border-radius: 0;
  }
  .main-detail__col1 .lesson-list .lesson-title, .main-detail__col3 .lesson-list .lesson-title {
    background: none;
    border-bottom: 1px solid #dbe2df;
    border-radius: 0;
    color: #011A11;
  }
  .main-detail__col1 .lesson-list .lesson-title::after, .main-detail__col3 .lesson-list .lesson-title::after {
    background-image: url("data:image/svg+xml;charset=UTF-8,%3csvg width='24' height='24' viewBox='0 0 24 24' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M20.2959 15.7957C20.1914 15.9006 20.0672 15.9838 19.9304 16.0406C19.7937 16.0974 19.6471 16.1266 19.499 16.1266C19.351 16.1266 19.2043 16.0974 19.0676 16.0406C18.9309 15.9838 18.8067 15.9006 18.7021 15.7957L12 9.09354L5.2959 15.7957C5.08455 16.0071 4.79791 16.1258 4.49902 16.1258C4.20014 16.1258 3.91349 16.0071 3.70215 15.7957C3.4908 15.5844 3.37207 15.2977 3.37207 14.9989C3.37207 14.7 3.4908 14.4133 3.70215 14.202L11.2021 6.70198C11.3067 6.5971 11.4309 6.51388 11.5676 6.4571C11.7043 6.40032 11.851 6.37109 11.999 6.37109C12.1471 6.37109 12.2937 6.40032 12.4304 6.4571C12.5672 6.51388 12.6914 6.5971 12.7959 6.70198L20.2959 14.202C20.4008 14.3065 20.484 14.4307 20.5408 14.5674C20.5976 14.7042 20.6268 14.8508 20.6268 14.9989C20.6268 15.1469 20.5976 15.2935 20.5408 15.4303C20.484 15.567 20.4008 15.6912 20.2959 15.7957Z' fill='%23DBE2DF'/%3e%3c/svg%3e");
  }
  .main-detail__col1 .lesson-list .lesson-title h3, .main-detail__col3 .lesson-list .lesson-title h3 {
    font-size: 16px;
    margin-bottom: 0;
  }
  .main-detail__col1 .lesson-list .card-body, .main-detail__col3 .lesson-list .card-body {
    padding: 0;
    border-radius: 0;
    border: 0;
    border-bottom: 1px solid #dbe2df;
  }
  .main-detail__col1 .lesson-list .lesson, .main-detail__col3 .lesson-list .lesson {
    padding-right: 10px;
  }
  .main-detail__col1 .lesson-list .lesson .title, .main-detail__col3 .lesson-list .lesson .title {
    font-size: 16px;
  }
}